import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Message } from '../models/message';
import { Observable } from 'rxjs/internal/Observable';
import { environment } from '../../environments/environment';
import { AppConfigService } from './app-config.service';

@Injectable({
  providedIn: 'root',
})
export class ConversationMessagesService {

  constructor(private http: HttpClient,
    private configService : AppConfigService
  ) {}

  generateSessionId() {
    const params = {};
    return this.http.post<any>(
      `${this.configService.getConfig()?.apiUrll}/start_session`,
      params
    );
  }

  getConversationMessages(participants: string[]) {
    const params = { participants };
    return this.http.post<any>(
      `${this.configService.getConfig()?.apiUrl}/api/v1/messages/getConversationMessages`,
      params
    );
  }

  sendMessage(message: Message): Observable<Message> {
    const params = {
      ...message,
    };
    return this.http.post<any>(
      `${this.configService.getConfig()?.apiUrl}/rag-chat`,
      params
    );
  }

  uploadFileIndex(formData: FormData): Observable<Message> {
    return this.http.post<any>(
      `localhost:5000/pass`,
      formData
    );
  }
}
